import React, { Component } from 'react';
import { Avatar, Button, Grid, Stack } from '@mui/material';
import AddCircleIcon from '@mui/icons-material/AddCircle';
import RemoveCircleIcon from '@mui/icons-material/RemoveCircle';

class Medal extends Component {
    formatMedalLabel(type) {
        if (type === "gold") {
            return "Gold"
        } else if (type === "silver") {
            return "Silver"
        }
        return "Bronze";
    }
    render() {
        const { addMedal, removeMedal, country, type } = this.props;
        const disabled = !country[type] > 0
        return (
            <Grid className={"medal"} container sx={{my: 1}}>
                <Grid item justifyContent='start' xs={6}>
                    <Stack direction='row' spacing={1}>
                        <Avatar className={"medal-avatar-" + type} sx={{color: 'black'}}>{country[type]}</Avatar>
                        <span id='medals-label'>{this.formatMedalLabel(type)} Medals</span>
                    </Stack>
                </Grid>
                <Grid item justifyContent='start' xs={3}>
                    <Button variant='outlined' onClick={() => addMedal(country.id, type) }><AddCircleIcon></AddCircleIcon></Button>
                </Grid>
                <Grid item xs={3}>
                    <Button disabled={disabled} color='error' variant='outlined' onClick={() => removeMedal(country.id, type) }><RemoveCircleIcon></RemoveCircleIcon></Button>
                </Grid>
            </Grid>
        )
    }
}

export default Medal;