import React, { Component } from 'react';
import { Badge, Card, CardContent, Grid, Divider, IconButton, Stack, Typography } from '@mui/material';
import DeleteForeverIcon from '@mui/icons-material/DeleteForever';
import Medal from './Medal';

class Country extends Component {
    render() {
        const { addMedal, removeMedal, country, deleteCountry } = this.props;
        return (
            <Grid sx={{mt: 2}}>
                <Card sx={{minWidth: 350}}>
                    <CardContent>
                        <Stack direction="row" justifyContent="space-between">
                            <Badge className='name-badge' badgeContent={country.gold + country.silver + country.bronze} color="secondary">
                                <Typography varient="h3" className='name' alignItems="center">{country.name}</Typography>
                            </Badge>
                            <IconButton color='' onClick={() => deleteCountry(country.id) }><DeleteForeverIcon></DeleteForeverIcon></IconButton>
                        </Stack>
                        <Divider varient='middle' sx={{my: 2}}/>
                            <Medal country={ country } 
                                    addMedal={ addMedal }
                                    removeMedal={ removeMedal }
                                    type="gold">
                            </Medal>
                            <Medal country={ country } 
                                    addMedal={ addMedal }
                                    removeMedal={ removeMedal }
                                    type="silver">
                            </Medal>
                            <Medal country={ country } 
                                    addMedal={ addMedal }
                                    removeMedal={ removeMedal }
                                    type="bronze">
                            </Medal>
                    </CardContent>
                </Card>
            </Grid>
        )
    }
}

export default Country;