import React, { Component } from 'react';
import { Button, Dialog, Fab, TextField } from '@mui/material';
import AddIcon from '@mui/icons-material/Add';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';

class NewCountry extends Component {
    state = {
        country: "",
        open: false
    };
    handleOpen = () => {
        this.setState({ open:true })
    };
    handleClose = () => {
        this.setState({ open:false, country:"" })
    };
    handleChange = (e) => this.setState({ [e.target.name]: e.target.value});
    render() {
        return (
            <div>
                <Fab onClick={this.handleOpen} className='add-country' color="primary" aria-label="add">
                    <AddIcon />
                </Fab>
                <Dialog
                    open={this.state.open}
                    onClose={this.handleClose}
                    PaperProps={{
                        component: 'form',
                        onSubmit: (e) => {
                            e.preventDefault();
                            const formData = new FormData(e.currentTarget);
                            const formJson = Object.fromEntries((formData).entries());
                            const country = formJson.country;
                            this.props.addCountry(country);
                            this.handleClose();
                        },
                    }}
                >
                    <DialogTitle>Add Country</DialogTitle>
                    <DialogContent>
                        <DialogContentText>
                            To add a new country, enter the name
                        </DialogContentText>
                        <TextField
                            autoFocus
                            required
                            margin="dense"
                            id="country"
                            name="country"
                            value={ this.state.country }
                            onChange={ this.handleChange }
                            label="Country Name"
                            type="text"
                            fullWidth
                            variant="standard"
                        />
                    </DialogContent>
                    <DialogActions>
                        <Button onClick={this.handleClose} color="error">Cancel</Button>
                        <Button disabled={ this.state.country.trim().length === 0 } type="submit">Save</Button>
                    </DialogActions>
                </Dialog>
            </div>
        )
    }
}

export default NewCountry;