import React, {Component} from 'react';
import './App.css';
import {AppBar, Badge, Grid, Typography} from '@mui/material';
import Country from './components/Country';
import NewCountry from './components/NewCountry';


class App extends Component {
    state = {
        countries: [
            { id: 1, name: 'United States', gold: 2, silver: 2, bronze: 3 },
            { id: 2, name: 'China', gold: 3, silver: 1, bronze: 0 },
            { id: 3, name: 'Germany', gold: 0, silver: 2, bronze: 2 }
        ]
    }
    addCountry = (country) => {
        const { countries } = this.state;
        const id = countries.length === 0 ? 1 : Math.max(...countries.map(country => country.id)) + 1;
        const mutableCountries = countries.concat({ id: id, name: country, gold:0, silver:0, bronze:0 });
        this.setState({ countries:mutableCountries });
    }
    deleteCountry = (countryId) => {
        const countries = this.state.countries.filter(c => c.id !== countryId);
        this.setState({ countries:countries });
    }
    addMedal = (countryId, medalType) => {
        const countriesMutable = [...this.state.countries];
        const index = countriesMutable.findIndex((c) => c.id === countryId);
        countriesMutable[index][medalType] += 1;
        this.setState({ countries:countriesMutable });
    }
    removeMedal = (countryId, medalType) => {
        const countriesMutable = [...this.state.countries];
        const index = countriesMutable.findIndex((c) => c.id === countryId);
        countriesMutable[index][medalType] -= 1;
        this.setState({ countries:countriesMutable });
    }
    getTotalMedals = () => {
        const golds = this.state.countries.reduce((a, b) => a + b.gold, 0);
        const silvers = this.state.countries.reduce((a, b) => a + b.silver, golds);
        return this.state.countries.reduce((a, b) => a + b.bronze, silvers);
    }
    render() {
        return (
            <div className="App">
                <AppBar position="static" sx={{mb: 2, p: 2}}>
                    <Typography variant="h4" color="inherit">
                        <Badge badgeContent={ this.getTotalMedals() } color="secondary">
                            Olympic Medals
                        </Badge>
                    </Typography>
                </AppBar>
                <Grid container justifyContent="center" columnSpacing={2}>
                    { this.state.countries.map(country => 
                        <Grid item key={ country.id } >
                            <Country 
                            country={ country } 
                            deleteCountry={ this.deleteCountry }
                            addMedal={ this.addMedal }
                            removeMedal={ this.removeMedal }/>
                        </Grid>
                    )}
                </Grid>
                <NewCountry addCountry={ this.addCountry } />
            </div>
        );
    }
}

export default App;
